import {useState} from 'react';
import { Icon24Newsfeed } from '@vkontakte/icons';
import './components/css/auth.css'
import {checkPassword, getPassword, getUser, sendCode, signIn, signUp} from './components/tdlib)/AuthBackend'
import api from './components/tdlib)/api'
import InputMask from 'react-input-mask';
import styled from 'styled-components'

function Auth(){
  const [number, setNumber] = useState('')
  const [styleNumber, setStyleNumber] = useState({display:'flex'})
  const [key, setKey] = useState('')
  const [styleKey, setStyleKey] = useState({display:'none'})
  const [pass, setPass] = useState('')
  const [stylePass, setStylePass] = useState({display:'none'})

  async function Auth(){

  setStyleNumber({display:'none'})
  setStyleKey({display:'flex'})

  const user = await getUser();
  const phone = number;
  const code = key;

  if (!user) {
    const { phone_code_hash } = await sendCode(phone);

    try {
      const signInResult = await signIn({
        code,
        phone,
        phone_code_hash,
      });

      if (signInResult._ === 'auth.authorizationSignUpRequired') {
        await signUp({
          phone,
          phone_code_hash,
        });
      }
    } catch (error) {
      if (error.error_message !== 'SESSION_PASSWORD_NEEDED') {
        console.log(`error:`, error);
        return;
      }
    setStyleKey({display:'none'})
    setStylePass({display:'flex'})
    }
  }
}
async function twoFactor(){
    const password = pass
    const { srp_id, current_algo, srp_B } = await getPassword();
    const { g, p, salt1, salt2 } = current_algo;

    const { A, M1 } = await api.mtproto.crypto.getSRPParams({
        g,
        p,
        salt1,
        salt2,
        gB: srp_B,
        password,
      });

    const checkPasswordResult = await checkPassword({ srp_id, A, M1 });
}
    return(
      <>
        <div style={styleNumber} className='main_wrapper'>
            <div className='logo_inner center'>
                <Icon24Newsfeed width={85} height={85} fill='FF7B92'></Icon24Newsfeed>
            </div>
            <h1>Reader X</h1>
            <div className='auth_form'>
                <InputMask type="tel" id='phone_number' className='phone_number' maskPlaceholder="" mask="9 (999) 999-99-99" onChange={(e) => setNumber(e.target.value)} />
                <label className='label_phone_number' htmlFor="phone_number">Your phone number</label>
            </div>
            <button onClick={Auth}>NEXT</button>
        </div>
        <div style={styleKey} className="main_wrapper">
           <InputMask type="tel" id='phone_number' className='phone_number' maskPlaceholder="" maskChar={null} mask="99999" onChange={(e) => setKey(e.target.value)} />
           <label className='label_phone_number' htmlFor="phone_number">Code</label>
          <button onClick={Auth}></button>
        </div>
        <div style={stylePass} className="main_wrapper">
          <input placeholder='password' type="text" id='phone_number' className='phone_number' onChange={(e) => setPass(e.target.value)}/>
          <button onClick={twoFactor}></button>
        </div>
      </>
    )
}

export default Auth
