import {
  useContext,
  createContext,
  useState
} from 'react'
import api from './api'
const TelegramContext = createContext()


function TelegramProvider (props) {
  const [state, setState] = useState('')
  if (state === ''){
    setstate()
  }
  function setstate() {
  api.call('users.getFullUser', {
    id: {
      _: 'inputUserSelf',
    },
  })
  .then(response => {
    setState('fulfilled')
  })    
  .catch(error => {
    setState('rejected')
  });
  }
  const test = 'sxndwl'
     return (
    <TelegramContext.Provider
      value={{
          state,
          test
      }}
      {...props}
    />
  )
}
function useTdlib () {
  const context = useContext(TelegramContext)
  if (context === undefined) {
    throw new Error(`api must be used within a TelegramProvider (check index.js) pls`)
  }
  return context
}

export { TelegramProvider, useTdlib }