import Auth from './Auth'
import MainApp from './MainApp' 
import Loading from './Loading' 
import {useTdlib} from './components/tdlib)/Context'

function App() {
  const {state} = useTdlib()
  if(state === 'rejected'){
    return <Auth/>
  } else if(state === 'fulfilled') {
    return <MainApp/>
  } else {
    return <Loading/>
  }
}

export default App;
